import React, { useState } from 'react';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import TopPannel from '../TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import { AiOutlineRight } from 'react-icons/ai';
import { Backdrop } from '@material-ui/core';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function DownloadReports(props) {
  const [user, setUser] = useState({});
  const [clientData, setClientData] = useState({});
  const [loading, setLoading] = useState(true);
  const [downloadingReport, setDownloadingReport] = useState(false);
  const [isInvested, setIsInvested] = useState(false)

  React.useEffect(() => {
    try {
      db.get(db.model.user).then(async (user) => {
        await getPortfolio(user.userData.id)
        setUser(user.userData)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  }, [])

  const getPortfolio = async (id) => {
    await db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setIsInvested(aa.MintboxConsolidated.length + aa.ETFConsolidated.length + aa.FundsConsolidated.length !== 0)
          db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setIsInvested(p.MintboxConsolidated.length + p.ETFConsolidated.length + p.FundsConsolidated.length !== 0)
      }
    })
  }

  const onClickPortfolioReport = async () => {
    setDownloadingReport(true);
    const res = await AdminServices.getPortfolioReport(user.id).then(res => res)
    console.log(res)
    if (res.type) {
      window.open(res.data.Report_URL)
      toast.success('Report downloaded')
      setDownloadingReport(false)
    } else {
      toast.error('Report not found')
      setDownloadingReport(false)
    }
  }

  const onClickTransactionReport = async () => {
    setDownloadingReport(true);
    const res = await AdminServices.getTransactionReport(user.id).then(res => res)
    console.log(res)
    if (res.type) {
      window.open(res.data.Report_URL)
      toast.success('Report downloaded')
      setDownloadingReport(false)
    } else {
      toast.error('Report not found')
      setDownloadingReport(false)
    }
  }

  return (
    <div className='mainContainer'>
      <MainHeader header='Menu' right='What are mintboxes?' rightAction={() => { }} navigation={props.history} />
      {
        loading ?
          <ActivityLoaderMintbox />
          :
          <div className='dataContainer px-0'>
            <TopPannel />
            {
              isInvested ?
                <>
                  <div className='menu-card'>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_report.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                      <div className='bodyTextDark'>Portfolio report</div>
                      <div className='header2dark secondaryGreen'
                        onClick={() => onClickPortfolioReport()}>Tap to download <AiOutlineRight size={12} /></div>
                    </div>
                  </div>
                  <div className='menu-card'>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_report.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                      <div className='bodyTextDark'>Transaction report</div>
                      <div className='header2dark secondaryGreen' onClick={() => onClickTransactionReport()}>Tap to download <AiOutlineRight size={12} /></div>
                    </div>
                  </div>
                  {/* <div className='menu-card' onClick={() => props.history.push('/menu/reports/capital-gains')}>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_report.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                      <div className='bodyTextDark'>Capital gains report</div>
                      <div className='smallTextLight'>The report your CA needs every year</div>
                      <div className='header2dark secondaryGreen'>Tap to select financial year <AiOutlineRight size={12} /></div>
                    </div>
                  </div>
                  <div className='menu-card' onClick={() => props.history.push('/menu/reports/ELSS')}>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_report.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                      <div className='bodyTextDark'>ELSS report</div>
                      <div className='smallTextLight'>The report your HR needs every year</div>
                      <div className='header2dark secondaryGreen'>Tap to select financial year <AiOutlineRight size={12} /></div>
                    </div>
                  </div> */}
                </>
                :
                <div style={{display:'flex', padding:'4%', margin:'4%', border:'1px solid '+cssVar.getPropertyValue('--light'), borderRadius:'8px', backgroundColor:cssVar.getPropertyValue('--lightest')}}>
                  <div className='bodyTextLight' style={{flex:1}}>Your reports will be available once you <span className='secondaryGreen' onClick={()=>props.history.push('/Invest')}>start investing</span></div>
                  <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_report.png' width={30} height={30} style={{margin:'2% 4%'}}/>
                </div>
            }
          </div>
      }
      <Backdrop style={{ color: '#fff', zIndex: 1021 }} open={downloadingReport}>
        <ActivityLoaderMintbox />
      </Backdrop>
    </div>
  )
}
